.to-dentro-blue {
    border-radius: 50px;
    background: #2d23a6;
    white-space: nowrap;
    padding: 10px 22px;
    color: white;
    font-size: 20px;
    outline: none;
    border: none;
    /* border: 1px solid #cecece; */
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-decoration: none;
}

.to-dentro-blue:hover {
    transition: all 0.2s ease-in-out;
    background: #1e195a;
    color: white;
}

.to-dentro-blue:active {
    color: white;
}