.accordion__section {
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    align-items: center; */
    max-width: 600px;
    border: white solid 1px;
    border-radius: 3px;
}

.accordion {
    background-color: white;
    color: black;
    cursor: pointer;
    padding: 28px;
    display: flex;
    align-items: center;
    border: none;
    outline: none;
    transition: background-color 0.6 ease;
}

.accordion:hover, .accordion.active {
    background-color: #efeded;
}



/* .accordion .active {
    background-color: #cecece;
} */

.accordion__title {
    font-weight: 600;
    font-size: 18px;
    text-align: left;
    max-width: 400px;



}

    @media screen and (max-width: 535px) {
        
        .accordion__title {
            max-width: 240px;            
        }

    }

.accordion__icon {
    margin-left: auto;
    transition: -webkit-transform 0.6s ease;
    transition: transform 0.6s ease;
    transition: transform 0.6s ease, -webkit-transform 0.6s ease;
}

.rotate {
    -webkit-transform: rotate(90deg);
            transform: rotate(90deg);
}

.accordion__content {
    background-color: #efeded;
    overflow: hidden;
    transition: max-height 0.6s ease;
}

.accordion__text {
    font-weight: 400;
    font-size: 15px;
    padding: 18px 35px 30px 35px;
    color: #292828;
}

hr {
    border: 0;
    clear:both;
    display:block;
    width: 80%;               
    background-color:#cecece;
    height: 1px;
    
  }
.module__section {
    display: flex;
    flex-direction: column;
    /* justify-content: center;
