    max-width: 600px;
    border: white solid 1px;
    border-radius: 5px;
    width: 100%;
}

.module {
    background-color: #2d23a6;
    color: white;
    cursor: pointer;
    padding: 32px;
    display: flex;
    border: none;
    align-items: center;
    outline: none;
    transition: background-color 0.6 ease;
    /* font-weight: 300; */
    border-radius: 5px;
}

.module:hover,
.active {
    background-color: #3930a7;
}

.module__title {
    font-weight: 400;
    font-size: 18px;
    text-align: left;
}

