.module__section {
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    align-items: center; */
    max-width: 600px;
    border: white solid 1px;
    border-radius: 5px;
    width: 100%;
}

.module {
    background-color: #2d23a6;
    color: white;
    cursor: pointer;
    padding: 32px;
    display: flex;
    border: none;
    align-items: center;
    outline: none;
    transition: background-color 0.6 ease;
    /* font-weight: 300; */
    border-radius: 5px;
}

.module:hover,
.active {
    background-color: #3930a7;
}

.module__title {
    font-weight: 400;
    font-size: 18px;
    text-align: left;
}

@media screen and (max-width: 512px) {

    .module__title {
        max-width: 200px;        
    }
}

@media screen and (max-width: 335px) {

    .module__title {
        max-width: 170px;        
    }
}

.module__icon {
    margin-left: auto;
    transition: transform 0.6s ease;
}

.rotate {
    transform: rotate(90deg);
}

.module__content {
    background-color: #3930a7;
    overflow: hidden;
    transition: max-height 0.6s ease;
    border-radius: 5px;
    /* padding-bottom: 20px;
    padding-left: 5px;
    padding-right: 5px;
    padding-top: 0; */
}

.module__text {
    font-weight: 400;
    font-size: 18px;
    padding: 0px 20px 20px 20px;
}